import React, { useState } from "react";
import { Button, Form, Spinner } from "react-bootstrap";
import logo from "../../../assets/images/main-logo.svg";
import { SocialLogin } from "../Social";
import { useDispatch, useSelector } from "react-redux";
import { requestLogin, requestRecoverLink } from "../../../../redux/actions";
import { getValidationErrors } from "../../../../utills/helpers";
import { EmailAddressValidation } from "../../../validations";

const ForgotPasswordFormComponent = ({ onLogin }) => {
  const [email, setEmail] = useState("");
  const [errors, setErrors] = useState("");
  const dispatch = useDispatch();
  const { isLoadingAuth: isLoading } = useSelector(
    (state) => state.authReducer
  );
  /**
   *
   * @param {*} e
   */
  const onInputChange = (e) => {
    //
    const { value } = e.target;
    setEmail(value);
    setErrors({
      email:""
    })
  };
  /**
   *
   * @param {*} e
   */
  const login = (e) => {
    e.preventDefault();
    try {
      EmailAddressValidation.validateSync({email}, {
        abortEarly: false,
      });
      dispatch(requestRecoverLink({email}));
    } catch (error) {
      setErrors(getValidationErrors(error));
    }
  };
  return (
    <>
      <div className="auth-form-wrap">
        <div className="auth-logo text-center mb-2 mt-2">
          <img src={logo} width={160} alt={"Main Logo"} />
        </div>
        <h5 className="mb-3 text-center text-capitalize">
          Forgot Your Password
        </h5>
        <p className="text-center">Enter your email below and we will send you instructions on how to reset your password</p>
        <Form onSubmit={login} noValidate>
          <Form.Group controlId="formBasicEmail">
            <Form.Control
              type="email"
              placeholder=""
              onChange={onInputChange}
              name="email"
              value={email}
            />
            {/* <Form.Label>Email</Form.Label> */}
            <label className={`form-control-placeholder ${email ? "float-label" : ""}`}>
              Email
            </label>
            {errors.email ? (
              <p className="text-danger">{errors.email}</p>
            ) : null}
          </Form.Group>
          <div className="auth-links d-block d-md-flex  text-center align-items-center justify-content-between mb-3">
            <p className="mb-3 mb-md-0">
              Remember your password?
              <a
                href="/"
                onClick={(e) => {
                  e.preventDefault();
                  onLogin();
                }}
              >
                {" "}
                Login
              </a>
            </p>
          </div>
          <Button
            variant="primary"
            type="submit"
            className="w-100"
            disabled={isLoading}
          >
            {isLoading ? (
              <>
                <Spinner />
                &nbsp; Please wait...
              </>
            ) : (
                <>Submit</>
              )}{" "}
          </Button>
          <SocialLogin />
        </Form>
      </div>
    </>
  );
};

export default ForgotPasswordFormComponent;
